import React, {useState} from 'react'
import { DiagonalContainer} from 'react-diagonal-container'
import './App.css';

function App() {
  const first = (
  <div className={'logo-container'}>
    <svg width="100" height="100">
      <rect className={'top'} x="12.5" y="5" width="75" height="25"/>
      <rect className={'mid'} x="12.5" y="31" width="75" height="30"/>
      <rect className={'bottom'} x="12.5" y="62" width="75" height="27"/>
    </svg>
    <h1>react-diagonal-container</h1>
    <div className="second">
      <h2>Installation and Usage</h2>
      <code className="code-block">npm install react-diagonal-container</code>
      <code className="code-block">{"import { DiagonalContainer } from 'react-diagonal-container'"}</code>
      <code className="code-block">{"<DiagonalContainer magnitude='small' background='green' spacingAfter='50px' flip flattenTop>{yourContent}</DiagonalContainer>"}</code>
    </div>
    </div>)
  const second = (
    <div className="second">
      <h2>Installation and Usage</h2>
      <code className="code-block">npm install react-diagonal-container</code>
      <code className="code-block">{"import { DiagonalContainer } from 'react-diagonal-container'"}</code>
      <code className="code-block">{"<DiagonalContainer magnitude='small' background='green' spacingAfter='50px' flip flattenTop>{yourContent}</DiagonalContainer>"}</code>
    </div>
  )
  const [state, setState] = useState((
    {
      first:{
        magnitude:'small',
        flipped:false,
        flatTop:false,
        flatBottom:false,
        color:"#55286F",
        content:first,
        spacing:'100px'
      },
      second:{
        magnitude:'medium',
        flipped:true,
        flatTop:false,
        flatBottom:false,
        color:"#BC96E6",
        content:second,
        spacing:'0px'
      },
      third:{
        magnitude:'extreme',
        flipped:false,
        flatTop:false,
        flatBottom:false,
        color:"#D8B4E2",
        content:null,
        spacing:'50px'
      },
      fourth:{
        magnitude:'large',
        flipped:true,
        flatTop:false,
        flatBottom:false,
        color:"#AE759F",
        content:null,
        spacing:'175px'
      }
    }
  ))
  const changeState = (data) => {
    const stateCopy = Object.assign({}, state)
    stateCopy[data.id][data.change] = data.value
    setState(stateCopy)
  }
  const DisplayCode = ({id}) => (
    <div className="display-code">
      <code>
        {
          `<DiagonalContainer 
            magnitude='${state[id].magnitude}' 
            ${state[id].color ? "background='" + state[id].color +"'" : ""}
            ${state[id].flipped ? "flip" : ""}
            ${state[id].flatTop ? "flattenTop" : ""}
            ${state[id].flatBottom ? "flattenBottom" : ""}
            ${state[id].spacing ? "spacingAfter='" + state[id].spacing + "'" : ""}>`
        }
      </code>
    </div>
  )
  return (
    <div className="App">
      {
        Object.keys(state).map((num)=>(
          <div key={num} className="root">
          <DiagonalContainer magnitude={state[num].magnitude} background={state[num].color} spacingAfter={state[num].spacing} flip={state[num].flipped} flattenTop={state[num].flatTop} containerBackground="transparent" flattenBottom={state[num].flatBottom}>
          {
            state[num].content
          }
          <div className="radio-container">
          {
            ["tiny", "small", "medium", "large", "extreme"].map((mag)=>(
                  <div key={mag}>
                    <input id={mag + "-" + num} checked={mag===state[num].magnitude} name={"magnitude-" + num}  type="radio" value={mag} onChange={(e)=>{changeState({id:num, change:'magnitude', value:e.currentTarget.value})}}/>
                    <label className="radio-label" htmlFor={mag + "-" + num}>{mag}</label>
                  </div>
              ))        
          }
          </div>
            <div className="checkboxes-container">
              <input id={"flipbox-" + num} type="checkbox" onClick={()=>{changeState({id:num, change:'flipped', value:!state[num].flipped})}}/>
              <label className="checkbox-label" htmlFor={"flipbox-" + num}>flip</label>
              <input id={"flatTop-" + num} type="checkbox" onClick={()=>{changeState({id:num, change:'flatTop', value:!state[num].flatTop})}}/>
              <label className="checkbox-label" htmlFor={"flatTop-" + num}>flat top</label>
              <input id={"flatBottom-" + num} type="checkbox" onClick={()=>{changeState({id:num, change:'flatBottom', value:!state[num].flatBottom})}}/>
              <label className="checkbox-label" htmlFor={"flatBottom-" + num}>flat bottom</label>
            </div>
            <div className="space-after-container">
              <label className="space-after" htmlFor={"spaceAfter-" + num}>space after</label>
              <input id={"spaceAfter-" + num} type="range" min="0" max="250" onChange={(e)=>{changeState({id:num, change:"spacing", value:e.currentTarget.value + 'px'})}}/>
            </div>
            <DisplayCode id={num}/>
          </DiagonalContainer>
          </div>
        ))
      }
    </div>
  );
}

export default App;
